.chat{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    border-bottom: 1px solid lightgray;
    cursor: pointer;
}
.chat:hover{
    opacity: 0.8;
}
.chat__avatar{
    height: 35px !important;
    width: 35px!important;


}
.chat__info{
    padding-left: 5px;
    flex: 1;


}
.chat__info >h2{
    font-size:11px ;
    font-weight: 500;
}
.chat__info >p{
    font-size: 9px;
}
.chat__readIcon{
    color:#ed3b55 ;

}